.header {
  display: flex;
  justify-content: flex-end;
  /* padding: 27px 0px 27px 60px; */
  padding-top: 27px;
  padding-bottom: 27px;
}

.navigationLinks {
  cursor: pointer;
  display: flex;
  gap: 33px;
}

.navigationLink {
  text-decoration: none;
  color: #fff;
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
}

.navigationLink img {
  width: 30px;
}

@media (max-width: 437px) {
  .navigationLinks {
    gap: 20px;
  }

  .navigationLink {
    text-decoration: none;
    color: #fff;
    display: flex;
    gap: 10px;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
  }

  .navigationLink img {
    width: 20px;
  }
}
